export default (state = {}, action) => {
  switch (action.type) {
    case 'USER_GET_PERMISSIONS_REQUEST':
      return {
        ...state,
        pending: true
      };

    case 'USER_GET_PERMISSIONS_RESPONSE':
      return {
        ...state,
        pending: false,
        ...action.payload
      };

    default:
      return state;
  }
};



