import * as pageApi from './fbApi';
import * as userApi from './fbUserApi';
import initApp from './fbInitApp';
import switchView from './switchView';

export default (config) =>
  (dispatch, getState) => {
    dispatch(switchView('loading'));
    dispatch(loginStatusFlow());
  };

const loginStatusFlow = () =>
  async (dispatch, getState) => {
    await initApp();

    const response = await dispatch(userApi.getLoginStatus());
    if (response.status === 'connected') {
      await dispatch(pageApi.getPageId());

      dispatch(switchView('overview'));
    }
    else {
      dispatch(switchView('login'));
    }
  };



